import YouTube from 'react-youtube';
import styles from './styles.css';
import images from '../../images';

// eslint-disable-next-line
export default ({ onNext, transitionState }) => (
	<div>
		{transitionState === 'entered' && (
			<YouTube onEnd={onNext} videoId="8ndhidEmUbI" className={styles.video} />
		)}
		<img className={styles.button} src={images.revealButton} />
	</div>
);



