import { Component } from 'preact';
import styles from './styles';
import Body from '../body';
import images from '../../images';
import { PARTS, OPTIONS } from '../../consts';

const STYLE_ADJUSTEMENTS = {
	[PARTS.TORSO]: styles.torsoOptionWrapper
};

class Builder extends Component {
  state = {
  	partSelected: null,
  	partsComplete: {
  		[PARTS.HEAD]: null,
  		[PARTS.TORSO]: null,
  		[PARTS.UPPER]: null,
  		[PARTS.LOWER]: null
  	}
  };

  getPageTitle() {
  	if (this.state.partSelected) {
  		const labels = {
  			[PARTS.HEAD]: 'Choose your vision',
  			[PARTS.TORSO]: 'Choose your strength',
  			[PARTS.UPPER]: 'Choose your speed',
  			[PARTS.LOWER]: 'Choose your technique'
  		};
  		return labels[this.state.partSelected];
  	}
  	const values = Object.values(this.state.partsComplete).filter(Boolean);
  	if (values.length) {
  		return `${values.length / 4 * 100}% complete`;
  	}
  	return 'Choose your skill';
  }

  render() {
  	const isComplete =
      Object.values(this.state.partsComplete).filter(Boolean).length === 4;
  	return (
  		<div style={styles.wrapper}>
  			{this.state.partSelected ? (
  				<div className={styles.optionsWrapper}>
  					{OPTIONS[this.state.partSelected].map(({ image, label }) => (
  						<div
  							onClick={() =>
  								this.setState({
  									partSelected: null,
  									partsComplete: {
  										...this.state.partsComplete,
  										[this.state.partSelected]: true
  									}
  								})
  							}
  							className={
  								styles.optionWrapper +
                  ' ' +
                  STYLE_ADJUSTEMENTS[this.state.partSelected]
  							}
  						>
  							<img className={styles.optionImage} src={image} />
  							<label className={styles.optionLabel}>{label}</label>
  						</div>
  					))}
  				</div>
  			) : (
  				<Body
  					partsComplete={this.state.partsComplete}
  					onClickHead={() => this.setState({ partSelected: PARTS.HEAD })}
  					onClickTorso={() => this.setState({ partSelected: PARTS.TORSO })}
  					onClickUpper={() => this.setState({ partSelected: PARTS.UPPER })}
  					onClickLower={() => this.setState({ partSelected: PARTS.LOWER })}
  				/>
  			)}
  			<div className={styles.pageFooter}>
  				<div
  					className={
  						styles.pageTitle +
              ' ' +
              (isComplete && !this.state.partSelected && styles.titleComplete)
  					}
  				>
  					{this.getPageTitle()}
  				</div>
  				{isComplete && (
  					<img
  						onClick={this.props.onNext}
  						className={styles.button}
  						src={images.nextButton}
  					/>
  				)}
  			</div>
  		</div>
  	);
  }
}

export default Builder;



