import { Component } from 'preact';
import Transition from 'react-transition-group/Transition';
import domtoimage from 'dom-to-image';
import './style/index.css';
import PageLayout from './components/page-layout';
import Widget from './components/widget';
import styleSettings from './style/settings';
import StepIntro from './components/step-intro';
import StepBuilder from './components/step-builder';
import StepVideo from './components/step-video';
import StepResult from './components/step-result';
import images from './images';

const STEPS = [
	{ view: StepIntro },
	{ view: StepBuilder },
	{ view: StepVideo },
	{ view: StepResult }
];

export default class App extends Component {
  state = {
  	step: null,
  	exitingStep: null,
  	isLoading: true
  };

  // eslint-disable-next-line
  componentWillMount() {
  	Promise.all(
  		Object.keys(images).map(
  			imageKey =>
  				new Promise(resolve => {
  					const imagePath = images[imageKey];
  					const image = new Image();
  					image.onload = () => {
  						resolve();
  					};
  					image.onerror = resolve;
  					image.src = imagePath;
  				})
  		)
  	).then(() => this.setState({ isLoading: false }));
  }

  goTo = step => () => {
  	if (this.state.exitingStep === null) {
  		this.setState({ step, exitingStep: this.state.step });
  	}
  };

  handleExit = () => {
  	this.setState({ exitingStep: null });
  };

  handleCreateImage = () => {
  	domtoimage
  		.toJpeg(document.getElementById('page-layout'), { quality: 0.95 })
  		.then(dataUrl => {
  			let link = document.createElement('a');
  			link.download = 'my-image-name.jpeg';
  			link.href = dataUrl;
  			link.click();
  		})
  		.catch(error => {
  			console.error('oops, something went wrong!', error);
  		});
  };

  initFirstStep() {
  	this.setState({ step: 0 });
  }

  componentDidMount() {
  	this.initFirstStep();
  }

  render() {
  	return (
  		<Widget isLoading={this.state.isLoading}>
  			<Transition
  				in={this.state.step === 2}
  				timeout={parseInt(styleSettings.transitionDuration, 10)}
  			>
  				{outerTransitionState => (
  					<PageLayout transitionState={outerTransitionState}>
  						{STEPS.map(({ view: View }, index) => (
  							<Transition
  								unmountOnExit
  								in={
  									this.state.step === index && this.state.exitingStep === null
  								}
  								timeout={parseInt(styleSettings.transitionDuration, 10)}
  								onExited={this.handleExit}
  							>
  								{state => (
  									<View
  										transitionState={state}
  										onNext={this.goTo(index + 1)}
  									/>
  								)}
  							</Transition>
  						))}
  					</PageLayout>
  				)}
  			</Transition>
  		</Widget>
  	);
  }
}



