import styles from './styles';
import images from '../../images';

const TITLE_TRANSITION_STYLE = {
	entering: styles.titleEntering,
	entered: styles.titleEntered
};

const BUTTON_TRANSITION_STYLE = {
	entering: styles.buttonEntering,
	entered: styles.buttonEntered
};

// eslint-disable-next-line
export default ({ transitionState, onNext }) => (
	<div
		className={styles.wrapper}
		style={{ backgroundImage: `url(${images.introForeground})` }}
	>
		<h1
			className={`${styles.title}
			${TITLE_TRANSITION_STYLE[transitionState]}`}
		>
      Build your own football legend
		</h1>
		<img
			onClick={onNext}
			src={images.startButton}
			className={`${styles.button}
			${BUTTON_TRANSITION_STYLE[transitionState]}`}
		/>
	</div>
);



