// @flow
import React from 'react';
import styles from './styles.css';
import images from '../../images';

const LOGO_TRANSITION_STATES = {
	entering: styles.logoEntering,
	entered: styles.logoEntered
};

// eslint-disable-next-line
export default ({ transitionState, children }) => (
	<div id="page-layout" className={styles.pageLayout}>
		<div
			className={styles.widgetWrapper}
			style={{ backgroundImage: `url(${images.background})` }}
		>
			<img
				className={`${styles.logo} ${LOGO_TRANSITION_STATES[transitionState]}`}
				src={images.logo}
			/>
			{children}
		</div>
	</div>
);



