import styles from './styles';
import { PARTS } from '../../consts';
import images from '../../images';

const HEAD_HEIGHT = 50;
const TORSO_HEIGHT = 130;
const UPPER_HEIGHT = 100;
const LOWER_HEIGHT = 90;
const SPACER = 10;

// eslint-disable-next-line
export default props => (
	<div className={styles.wrapper}>
		<div className={styles.inner}>
			<img className={styles.body} src={images.body} />
			<img
				className={styles.body}
				style={{ opacity: props.partsComplete[PARTS.HEAD] ? 1 : 0 }}
				src={images.head}
			/>
			<img
				className={styles.body}
				style={{ opacity: props.partsComplete[PARTS.TORSO] ? 1 : 0 }}
				src={images.torso}
			/>
			<img
				className={styles.body}
				style={{ opacity: props.partsComplete[PARTS.UPPER] ? 1 : 0 }}
				src={images.upperLeg}
			/>
			<img
				className={styles.body}
				style={{ opacity: props.partsComplete[PARTS.LOWER] ? 1 : 0 }}
				src={images.lowerLeg}
			/>
			<div
				onClick={props.onClickHead}
				className={styles.sectionWrapper}
				style={{ height: `${HEAD_HEIGHT}px` }}
			>
				<img src={images.gradient} className={styles.gradient} />
				<div className={styles.sectionLabel}>Vision</div>
			</div>

			<div
				onClick={props.onClickTorso}
				className={styles.sectionWrapper}
				style={{
					height: `${UPPER_HEIGHT}px`,
					top: `${HEAD_HEIGHT + SPACER}px`
				}}
			>
				<img src={images.gradient} className={styles.gradient} />
				<div className={styles.sectionLabel}>Strength</div>
			</div>

			<div
				onClick={props.onClickUpper}
				className={styles.sectionWrapper}
				style={{
					height: `${UPPER_HEIGHT}px`,
					top: `${TORSO_HEIGHT + HEAD_HEIGHT + SPACER * 2}px`
				}}
			>
				<img src={images.gradient} className={styles.gradient} />
				<div className={styles.sectionLabel}>Speed</div>
			</div>

			<div
				onClick={props.onClickLower}
				className={styles.sectionWrapper}
				style={{
					height: `${LOWER_HEIGHT}px`,
					top: `${HEAD_HEIGHT + TORSO_HEIGHT + UPPER_HEIGHT + SPACER * 3}px`
				}}
			>
				<img src={images.gradient} className={styles.gradient} />
				<div className={styles.sectionLabel}>Technique</div>
			</div>
		</div>
	</div>
);



